header, footer{
  background: #b2dcfb;
}
.navBar{
  
}
.navBar>ul{
  padding-left: 0;
}
.navBar>ul>li{
  display: inline-block;
  list-style-type: none;
  padding: 0 1rem;
}
.navBar>ul>li>a{
  color: #FFF;
  text-decoration: none;
  font-weight: 500;
}
.navBar>ul>li>a:hover, .active{
  color: #000 !important;
}
.workExperiencePara{
  margin-top: 2rem!important;
}
.workExperiencePara:last-child{
  margin-top: .5rem!important;
}
.headerText, .educationPara p, .workExperiencePara p{
  font-style: italic;
  color: #5899c8;
}
.mediaName{
  font-size: 20px;
  font-weight: 600;
  color: #5899c8;
  font-style: italic;
  text-transform: uppercase;
}
.spanHeader{
  position: relative;
  top: .5rem;
}
.visitIcon{
  display: block;
  background: rgba(0, 0, 0, 0.35);
  padding: 1rem;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}
.visitIcon:hover::after{
  top: -100%;
}
.visitIcon:after{
    content: '';
    position: absolute;
    background: rgba(0, 0, 0, 0.50);
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1;
    height: 100%;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    
}
.visitIcon i{
  font-size: 10rem;
  color: #FFF;
}
.skillSetTags{

}
.skillSetTags span{
  background: #5899c8;
  color: #FFF;
  padding: .2rem 1rem;
  border-radius: 5px;
  margin-top: .5rem;
  display: inline-block;
  margin-left: 1rem;
  font-style: italic;
}
footer p{
  margin-top: 1rem;
  color: #000;
  font-weight: 600;
}
.followPara a{
  font-size: 1.5rem;
  padding: 0 .2rem;
  color: #000;
}
.designedAnch a{
  border-bottom: solid 1px;
  text-decoration: none !important;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

